body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  border-radius: 10px;
  margin: 50px auto;
  padding: 30px;
  box-shadow: 0 0 20px rgba(0, 0, 0, .1);
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 28px;
}

.breed-select {
  width: 100%;
  color: #333;
  background-color: #f8f8f8;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  margin-bottom: 20px;
  padding: 12px;
  font-size: 16px;
  transition: background-color .3s;
}

.breed-select:hover {
  background-color: #e0e0e0;
}

.loader, .error {
  text-align: center;
  color: #e74c3c;
  margin: 20px 0;
  font-size: 18px;
  display: none;
}

.cat-info {
  text-align: center;
  display: none;
}

.cat-info img {
  max-width: 100%;
  border-radius: 10px;
  margin-bottom: 20px;
  box-shadow: 0 0 15px rgba(0, 0, 0, .2);
}

.cat-info h2 {
  color: #333;
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-info p {
  color: #666;
  font-size: 16px;
  line-height: 1.5;
}

